body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, input, textarea, th, td,
iframe {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}
body {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', SimSun, sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  /* background-color: #f8f8f8; */
}
a {
  color: #4078c0;
  text-decoration: none;
}
button, input, select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}
ul, ol {
  list-style: none;
}
code.hljs {
  line-height: 1.5;
  font-family: Source Code Pro, Menlo, Monaco, Consolas, Courier, monospace;
  font-size: 12px;
  padding: 20px;
  background-color: #2a3138;
  border: solid 1px #E5E5E5;
  margin-bottom: 25px;
  border-radius: 4px;
  -webkit-font-smoothing: auto;
}
.clearfix::before {
  display: table;
  content: "";
}
.clearfix::after {
  display: table;
  content: "";
  clear: both;
}
.main-content {
  width: 50%;
  margin: 50px 20px 40px;
  border: 1px solid #f2f2f2;
}
.main-content .page-tip {
  position: absolute;
  left: 50%;
  top: 60px;
  -webkit-transform: translatex(-50%);
          transform: translatex(-50%);
  height: 60px;
  line-height: 60px;
  font-size: 24px;
  color: #0067ed;
}
.page-container {
  background-color: #fff;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  overflow: hidden;
}
.page-content {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.page-content section {
  padding: 0 40px;
}
.page-content section > h1,
.page-content section > h2,
.page-content section > h3,
.page-content section > h4,
.page-content section > h5,
.page-content section > h6 {
  color: #333;
  line-height: 1.5;
  margin: 20px 0;
  font-weight: normal;
}
.page-content section > h1:hover a,
.page-content section > h2:hover a,
.page-content section > h3:hover a,
.page-content section > h4:hover a,
.page-content section > h5:hover a,
.page-content section > h6:hover a {
  opacity: .4;
}
.page-content section > h1 a,
.page-content section > h2 a,
.page-content section > h3 a,
.page-content section > h4 a,
.page-content section > h5 a,
.page-content section > h6 a {
  float: left;
  margin-left: -20px;
  opacity: 0;
  cursor: pointer;
}
.page-content section > h1 a:hover,
.page-content section > h2 a:hover,
.page-content section > h3 a:hover,
.page-content section > h4 a:hover,
.page-content section > h5 a:hover,
.page-content section > h6 a:hover {
  opacity: .4;
}
.page-content section > h1 {
  font-size: 34px;
}
.page-content section > h2 {
  font-size: 28px;
}
.page-content section > h3 {
  font-size: 22px;
}
.page-content section > h4 {
  font-size: 18px;
}
.page-content section > h5 {
  font-size: 16px;
}
.page-content section > h6 {
  font-size: 14px;
  color: #666;
}
.page-content section > p {
  font-size: 14px;
  line-height: 20px;
  color: #666;
  margin: 14px 0;
}
.page-content section > p a {
  color: #38f;
}
.page-content section > ul li,
.page-content section > ol li {
  color: #666;
  font-size: 14px;
  line-height: 20px;
  margin: 10px 0 10px 20px;
  padding-left: 20px;
  position: relative;
}
.page-content section > ul li a,
.page-content section > ol li a {
  color: #38f;
}
.page-content section > ul li::before,
.page-content section > ol li::before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 2px solid #999;
  border-radius: 50%;
  top: 6px;
  left: 0;
}
.page-content section > ul li li,
.page-content section > ol li li {
  margin-left: 0;
}
.page-content p > code,
.page-content .table code,
.page-content li > code {
  background-color: #F2F2F2;
  display: inline-block;
  border: 1px solid #E5E5E5;
  border-radius: 3px;
  padding: 1px 3px;
  color: #333;
  margin: 0 2px;
}
.table {
  border-collapse: collapse;
  width: 100%;
  background-color: #fff;
  color: #333;
  font-size: 14px;
  margin-bottom: 45px;
}
.table th {
  text-align: left;
  border: 1px solid #E5E5E5;
  background-color: #F2F2F2;
  padding: 10px;
}
.table th:first-child {
  padding-left: 10px;
}
.table td {
  border: 1px solid #E5E5E5;
  padding: 10px;
}
.l {
  float: left;
}
.r {
  float: right;
}
.gray {
  color: #999;
}
.placeholder {
  font-size: 14px;
  color: #cccccc;
}
.vui-intro {
  text-align: center;
}
.vui-intro__logo img {
  margin: 0 auto;
}
.vui-intro__title {
  font-size: 45px;
  line-height: 60px;
  font-weight: 400;
  font-family: monospace;
}
.vui-intro__subtitle {
  font-size: 15px;
  color: #455a64;
